<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 提示区域 -->
      <el-alert
        title="添加商品信息"
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>
      <!-- 步骤条 -->
      <el-steps
        :active="parseInt(activeIndex)"
        finish-status="success"
        align-center
      >
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- tab栏区域 -->
      <!-- tab点击不同的便签 会将 name 的值赋给 activeIndex 从而引起 上方步骤条的改变 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="基本信息" name="0">
            <!-- label->表单标题  prop->验证规则 -->
            <el-form-item label="商品名称" prop="goods_name">
              <!-- 输入框 -->
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <!-- 输入框 -->
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <!-- 输入框 -->
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商名数量" prop="goods_number">
              <!-- 输入框 -->
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单的item项 -->
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTableData"
              :key="item.attr_id"
            >
              <!-- 复选框组 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action 表示图片要上传到的后台API地址 -->
            <!-- on-preview->图片预览  on-remove->图片删除  -->
            <!-- list-type 文件列表的类型 渲染在网页上的样式 -->
            <!-- headers 设置上传的请求头部 -->
            <!-- on-success 文件上传成功时的钩子 -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编译器组件 -->
            <quill-editor v-model="addForm.goods_introduce" />
            <!-- 添加商品的按钮 -->
            <el-button type="primary" class="btnAdd" @click="add"
              >添加商品</el-button
            >
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 图片预览 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt="" class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
// 引入lodash
import _ from 'lodash'

export default {
  name: 'Add',
  data() {
    return {
      activeIndex: '0',
      // 添加商品的表单数据对象
      addForm: {
        goods_name: '', // 商品名称
        goods_price: 0, // 商品价格
        goods_weight: 0, // 商品重量
        goods_number: 0, // 商品数量
        goods_cat: [], // 商品所属的分类 数组
        pics: [], // 上传的图片临时路径（对象）
        goods_introduce: '', // 商品详情
        attrs: [], // 商品的参数（数组），包含 动态参数 和 静态属性
      },
      // 添加商品的表单验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' },
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'change' },
        ],
      },
      // 商品分类列表
      catelist: [],
      // 级联选择框配置选项
      cateProps: {
        // 显示的内容
        label: 'cat_name',
        // 选中的值
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover',
      },
      // 动态参数列表数据 用于多选框组的数据绑定(需要数组)
      manyTableData: [],
      // 静态属性列表数据
      onlyTableData: [],
      // 图片上传的API地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传组件的headers请求头对象
      headerObj: {
        Authorization: window.sessionStorage.getItem('token'),
      },
      // 预览图片地址
      previewPath: '',
      // 控制图片预览对话框的显示与隐藏
      previewVisible: false,
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取所有分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类数据失败! ')
      } else {
        this.catelist = res.data
        // console.log(this.catelist)
      }
    },
    // 级联选择器选中项变化, 会触发该函数
    handleChange() {
      // console.log(this.addForm.goods_cat);
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 切换第一个标签时的验证
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请先选择商品分类! ')
        return false
      }
    },
    // 监听el-tabs的切换方法
    async tabClicked() {
      // console.log(this.activeIndex);
      // 访问的是动态参数面板
      if (this.activeIndex === '1') {
        // addForm.goods_cat 是一个数组 三级分类是第三项 下标为2
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' },
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数列表失败! ')
        } else {
          // 输入当前选中三级菜单里面的数据
          // console.log(res.data)
          // 将data数据变成数组
          res.data.forEach((item) => {
            // 判断字符串长度是否为0
            item.attr_vals =
              item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
          })
          this.manyTableData = res.data
        }
      }
      // 访问的是静态属性
      else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' },
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取静态属性列表失败! ')
        } else {
          console.log(res.data)
          this.onlyTableData = res.data
        }
      }
    },
    // 处理图片预览效果
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 处理图片移除操作
    handleRemove(file) {
      // console.log(file)
      // 1. 获取将要删除的图片的临时路径
      const filePath = file.response.data.tmp_path
      // 2. 从 pics 数组中, 找到这个图片对应的索引值
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath)
      // 3. 调用数组的 splice 方法, 把图片信息对象, 从 pics 数组中移除
      this.addForm.pics.splice(i, 1)
      // console.log(this.addForm);
    },
    // 监听图片上传成功的事件
    // response 服务器返回的数据
    handleSuccess(response) {
      // console.log(response)
      // 1. 拼接得到一个图品信息对象
      const picInfo = { pic: response.data.tmp_path }
      // 2. 将图片信息对象, push 到 pics 数组中
      this.addForm.pics.push(picInfo)
      // console.log(this.addForm)
    },
    // 添加商品
    add() {
      this.$refs.addFormRef.validate(async (valid) => {
        // 校验失败
        if (!valid) {
          return this.$message.error('请填写必要的表单项! ')
        } else {
          // 执行添加的业务逻辑
          // lodash 深拷贝 cloneDeep(obj) 将原本的数据拷贝一份
          // 这里面提交的 goods_cat数据是 "以为','分割的分类列表" 但是 级联选择器里面双向绑定的数据需要是数组 两者不能同时满足
          const form = _.cloneDeep(this.addForm)
          form.goods_cat = form.goods_cat.join(',')
          // 处理动态参数
          this.manyTableData.forEach((item) => {
            const newInfo = {
              attr_id: item.attr_id,
              // attr_value 的值为字符串 但是attr_vals是一个数组
              attr_value: item.attr_vals.join(' '),
            }
            // 将动态参数追加进attrs数组中
            this.addForm.attrs.push(newInfo)
          })
          // 处理静态属性
          this.onlyTableData.forEach((item) => {
            const newInfo = {
              attr_id: item.attr_id,
              attr_value: item.attrs,
            }
            this.addForm.attrs.push(newInfo)
          })
          form.attrs = this.addForm.attrs
          console.log(form)

          // 发起请求 添加商品
          // 商品的名称, 必须是唯一的
          const { data: res } = await this.$http.post('goods', form)
          if (res.meta.status !== 201) {
            return this.$message.error('添加商品失败! ')
          } else {
            this.$message.success('添加商品成功! ')
            // 跳转到商品列表页面
            this.$router.push('/goods')
          }
        }
      })
    },
  },
  computed: {
    // 获取选中的三级分类的 Id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      } else {
        // 没有三级分类Id
        return null
      }
    },
  },
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 280px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>